body {
    background-color: black;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

canvas {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.App {
    display: flex;
    flex-flow: row nowrap;
    width: 100%;
    height: 400px;
}
.colCanvas {
    position: relative;
    margin-top: -250px;
    z-index: 0;
}

.fret {
    display: block;
    position: absolute;
    padding: 0;
    margin: 0;
}

.strings {
    width: 100%;
    position: relative;
    z-index: 2;
    display: flex;
    padding: 0;
    margin: 0;
    flex-flow: column nowrap;
    justify-content: space-between;
    align-items: stretch;
}

.string {
    display: flex;
    padding: 0;
    margin: 0;
    flex-flow: row nowrap;
    justify-content: space-between;
    margin: 0;
}

button {
    height: 100%;
    width: 100%;
    opacity: 0;
}

button:hover {
    opacity: 0.5;
}

.fretboardContainer {
    flex: 6 1 auto;
    width: 100%;
}

.select {
    width: 100%;
    height: 100%;
    flex-grow: 2;
}

.stringSelection {
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-evenly;
    flex: 1 4 auto;
    height: 100%;
    width: 100%;
}

.listSelect {
    display: flex;
    flex-grow: 1;
}

.removeString {
    opacity: 1;
    background-color: rgb(209, 135, 135);
    flex-grow: 1;
    border: 2px red solid;
}

.addString {
    opacity: 1;
    flex-grow: 1;
    background-color: rgb(87, 238, 87);
    border: 2px rgb(104, 172, 104) solid;
}

.topApp {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
}

.settings {
    border: 5px grey solid;
    margin: 10px;
    border-radius: 5px;
    background-color: rgb(204, 255, 255);
}

.settingsHeading {
    text-align: center;
}

h1 {
    color: white;
}